:root {
  --shadow-default: 0 2px 2px #00000024, 0 3px 1px -2px #0000001f, 0 1px 5px #0003;
  --shadow-hover: 0 3px 3px #00000024, 0 1px 7px #0000001f, 0 3px 1px -1px #0003;
}

body {
  height: 100vh;
  flex-direction: column;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
}

button, a {
  height: 36px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .5px;
  vertical-align: middle;
  cursor: pointer;
  color: #fff;
  box-shadow: var(--shadow-default);
  -webkit-tap-highlight-color: transparent;
  background-color: #26a69a;
  border: none;
  border-radius: 2px;
  outline: 0;
  padding: 0 16px;
  font-size: 14px;
  line-height: 36px;
  text-decoration: none;
  transition: background-color .2s ease-out;
  display: inline-block;
}

button:hover, a:hover {
  box-shadow: var(--shadow-hover);
  background-color: #2bbbad;
}

.wrapper {
  flex-grow: 1;
  align-items: center;
  margin-bottom: 100px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.wrapper button {
  height: 70px;
  border-radius: 5px;
  padding: 0 16px;
  font-size: 25px;
}

.wrapper button:last-child {
  background-color: #a62826;
  margin-left: 25px;
}

.wrapper button:last-child:hover {
  background-color: #bb392b;
  margin-left: 25px;
}

.wrapper button:last-child:disabled, .wrapper button:disabled {
  cursor: default;
  background: #818181;
}

.wrapper button:disabled:hover {
  box-shadow: var(--shadow-default);
}

